@import url(https://fonts.googleapis.com/css2?family=Over+the+Rainbow&family=Shadows+Into+Light&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Catamaran:wght@300;400;500;600;700&family=Over+the+Rainbow&family=Shadows+Into+Light&display=swap);
/* import Google font */


:root {
  --body-background:#fff;
  /* nav */
  --nav-background: hsl(220, 29%, 31%);
  /* connect */
  --connect-icon:#acb1be;
  --connect-icon-hover:hsl(0, 0%, 100%);
}

.btn-dark {
  background-color: hsl(220, 29%, 31%);
  background-color: var(--nav-background);
}

.btn-dark:hover {
  background-color: hsl(220, 29%, 41%);
}

body {
  font-family: 'Catamaran', sans-serif;
  background-color: #fff;
  background-color: var(--body-background);
}

.navbar {
  background-color: hsl(220, 29%, 31%);
  background-color: var(--nav-background);
}

#navLogo {
  font-family: 'Over the Rainbow', cursive;
  /* font-family: 'Shadows Into Light', cursive; */
}

.project-item {
  text-align: center;
}

.project-image1,
.project-image2,
.project-image3,
.project-image4,
.project-image5,
.project-image6,
.project-image7 {
  background-size:cover;
  background-repeat: no-repeat;
  background-position-x: center;
  height: 600px;
}

.project-image1 {
  background-image: url(/static/media/manageMeNow.f09faa6a.jpg);
}
.project-image2 {
  background-image: url(/static/media/enviropact.a29f70ad.jpg);
}
.project-image3 {
  background-image: url(/static/media/bike2work.065dd7d1.jpg);
}
.project-image4 {
  background-image: url(/static/media/readmeGenerator.08985666.jpg);
}
.project-image5 {
  background-image: url(/static/media/employeeTracker.32360ba6.jpg);
}
.project-image6 {
  background-image: url(/static/media/teamProfileGenerator.f17efc24.jpg);
}
.project-image7 {
  background-image: url(/static/media/javascript-quiz.32cc9c46.jpg);
}


/* MAIN BODY */
.jumbotron {
  color: black;
  background-image: url(/static/media/glitchbpopacity78.3ef7efc2.jpg);
  background-position: center;
  background-position-y: 25%;
  background-repeat: no-repeat;
  background-size: cover;
  height: 600px;
}

.connect-icon {
  color: #acb1be;
  color: var(--connect-icon);
}

.connect-icon:hover {
  color: hsl(0, 0%, 100%);
  color: var(--connect-icon-hover);
}

.footer {
  background-color: hsl(220, 29%, 31%);
  background-color: var(--nav-background);
  color: #acb1bd;
}

.nowrap {
  word-wrap: nowrap;
}

/* Contact */
.input-message {
  height:200px;
  padding-left: 3px;
}

/* RESUME PAGE */
#resume-frame {
  height: 90vh;
}

.download-pdf {
  color: hsl(220, 29%, 31%);
  color: var(--nav-background);
}
.download-pdf:hover {
  color: hsl(220, 29%, 51%);
}

.aboutContainer {
  height: 100vh;
}

@media screen and (max-width:768px) {
  
  .project-image1,
  .project-image2,
  .project-image3,
  .project-image4,
  .project-image5,
  .project-image6,
  .project-image7 {
  height: 400px;
}

}
